@import url('https://fonts.googleapis.com/css2?family=Vollkorn:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Acme&family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Aldrich&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Aldrich&family=Oxanium:wght@200..800&display=swap');
:root {
  --border: #00FFFF;
}


body {
  background-image: linear-gradient(to right, rgb(50, 62, 111), rgb(51, 84, 95));
  font-family: 'Poppins', sans-serif;
  margin: 0%;
  padding: 0%;
  background-color: #294055;
  color: white;
}

/* Navbar.css */

.nav_bar {
  height: 60px;
  position: fixed;
  width: 100%;
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  background: transparent;
  padding: 0 5rem;
  z-index: 10;
  transition: 1s;
  font-family: "Oxanium", sans-serif;
  font-weight: 600;
}

.nav-items {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
}

.nav_bar.active {
  background-color: #333;
  transition: 1s;
}

.nav_bar a {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  margin: 0 1rem;
  position: relative;
}

.nav_bar a::before {
  content: " ";
  width: 2rem;
  height: 0.2rem;
  background: var(--border);
  border-radius: .5rem;
  position: absolute;
  bottom: -0.1rem;
  opacity: 0;
  transform: translateX(-1.5rem);
  transition: all 0.3s ease;
}

.nav_bar a:hover::before {
  width: 100%;
  transform: translateX(0);
  opacity: 1;
}

a:hover,
a.active_link {
  color: var(--border);

}

#logo {
  font-family: 'Acme', sans-serif;
  border: 2px solid white;
  border-radius: .5rem;
  padding: 0 5rem;
  padding: 5px 6px 1px 6px;

}

/* home */

.home {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 100vh;
}

.home-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* font-size: 3vw; */
  margin-left: 5rem;
}

.home-name {
  font-family: 'Acme', sans-serif;
  font-size: 4vw;
  /* font-size: 2.5rem; */
}

.home-icon-container {
  display: flex;
 
}
.home-icon-container button{
  background-color: var(--border);
  color: white;
  border: none;
  border-radius:2rem ;
  padding: 0 1rem;
  margin-top: .6rem;
  margin-right: 1rem;
  font-family: "Aldrich", sans-serif;
  font-weight: 600;
  color: #333;
}
.home-icon-container button:hover{
  background-color: transparent;
  color: white;
  border: 1px solid white;
  transition: .5s;
}

.home-icon {
  border: 2px solid var(--border);
  height: 2.3rem;
  width: 2.3rem;
  font-size: 25px;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #00FFFF;
}

.home-icon:hover {
  color: #333;
  background-color: aqua;
  transition: .5s;
}

.home-img {
  display: flex;
  justify-content: center;
}

.home-img img {
  width: 87%;
  border-radius: 50%;

}



.resume-btn {
  background-color: var(--border);
  color: white;
  border: none;
  border-radius:.5rem ;
  padding: .5rem;
  font-family: "Aldrich", sans-serif;
  font-weight: 600;
  color: #333;

}

.resume-btn:hover {
  background-color:transparent;
  color: white;
  border: 1px solid white;
  transition: .5s;
}

.social-icons {
  margin-top: 20px;
  display: flex;
  margin: 6px;
  padding-right: 10px;
}


/* about */


.about-wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  text-align: justify;
}

.about-l{
  display: grid;
  grid-template-columns: 50% 50%;
}
.about-cards{
  padding: 2rem;
  margin: .5rem;
  background-color: #294055;
}

.about-r{
  margin: .5rem;
}

.about-name{
  font-family: 'Acme', sans-serif;
}

/* Add this CSS to your stylesheets */
.close-button {
  position: absolute;
  top: 10px;
  right: 11rem;
  background-color: white;
  border: 2px solid rgb(7, 7, 7);
  border-radius: 7px;
  color: #d21b1b;
  font-size: 24px;
  padding: 0 20px;
  cursor: pointer;
}

.close-button:hover {
  color: #ff0000; /* Change color on hover if desired */
}


/* Skills */

.skills-card {
  border-radius: .65rem;
  border: 1.5px solid var(--border);
  padding: 2.5rem;
  text-align: center;
  background:transparent;
  backdrop-filter: blur(1rem);
  position: relative;
  cursor: pointer;
  transition: all .3s ease;

}

.skills-card span {
  font-size: 1.3rem;
  font-weight: 500;
}

.skill-icon {
  width: 3rem;
  height: 3rem;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, rgb(50, 62, 111), rgb(51, 84, 95) 100%);
  border-radius: .65rem;
  border: 1.5px solid;
  position: absolute;
  top: -1rem;
  left: -1rem;
}

.skill-icon img {
  align-items: center;
  object-fit: contain;

}

.skills-card:hover,
.skills-card.active {
  background: linear-gradient(180deg, rgb(50, 62, 111), rgb(72, 118, 133) 100%);

}

.skills-container {
  display: flex;
  flex-direction: column;

}

.section-heading {
  font-size: 3vw;
  margin-bottom: 2rem;
  font-family: 'Acme', sans-serif;
}

.skills-content {
  display: flex;
  /* align-items: center; */
  justify-content: space-around;
  gap: 2rem;
}

.skills {
  /* flex: 1; */
  width: 60%;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

.skills-info {
  width: 40%;
  /* flex: 1; */
  border: 2px solid var(--border);
  border-radius: .65rem;
}


.skills-info-card h4 {
  border-bottom: 2px solid var(--border);
  padding: 1rem;
}

.skills-info-content {
  padding: 0rem 1rem;

}

/* Portfoilio */

.portfolio-cards-container {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: .5rem;
}

.card-container {
  position: relative;
  border: 1px solid var(--border);
  border-radius: 15px;
  height: 14rem;
}

div.desc {
  padding: 10px;
  text-align: center;
  position: absolute;
  border: 1px solid var(--border);
  border-radius: 0px .5rem 0px .5rem;
  bottom: 0px;
  background: linear-gradient(180deg, rgb(50, 62, 111), rgb(51, 84, 95) 100%);
  color: var(--border);
  font-size: 1.5vw;
}

.image {
  border-radius: 15px;
  opacity: 0.4;
  width: 100%;
  height: 100%;
  transition: .5s ease;
  backface-visibility: hidden;
}

.middle {
  transition: .5s ease;
  opacity: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}

.card-container:hover .image {
  opacity: 1;
}

.card-container:hover .middle {
  opacity: 1;
}

.text {
  
  color: black;
  font-size: 16px;
  /* padding: 16px 32px; */
  
}

/* Contact */

.contact-info {
  display: grid;
  grid-template-columns: 50% 50%;
}

.cont-info-wrap {
  border: 2px solid var(--border);
  border-radius: .5rem;
  margin: 2px;
  display: flex;
  justify-content: center;
}

.cont-links {
  border: 2px solid var(--border);
  border-radius: .5rem;
  display: grid;
}

form {
  border: 2px solid var(--border);
  border-radius: 10px;
}

/* .cont-field{
  display: flex;
  align-items: center;
} */
.cont-input {
  border: 2px solid var(--border);
  border-radius: 9px;
}

.cont-icon {
  border: 2px solid var(--border);
  height: 2.3rem;
  width: 2.3rem;
  font-size: 25px;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #00FFFF;
}

.cont-icon:hover {
  color: #333;
  background-color: aqua;
  transition: .5s;
}


.footer {
  width: 100%;
  border-top: 1px solid var(--border);
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.footer p {
  font-size: 13px;
  padding-top: 16px;
}

.page {
  height: 100vh;
}

